.Modal__backdrop {
  width: 100%;
  height: 100vh;
  position: fixed;
  background-color: rgba(39, 32, 35, 0.5);
  z-index: 20;
  top: 0;
  left: 0; }

.Modal__window {
  width: 60%;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100; }
  @media screen and (max-width: 768px) {
    .Modal__window {
      width: 95%; } }

.Modal_shown {
  -webkit-animation: showModal .5s ease forwards;
          animation: showModal .5s ease forwards; }

.Modal_hidden {
  -webkit-animation: hideModal .5s ease forwards;
          animation: hideModal .5s ease forwards; }

@-webkit-keyframes showModal {
  from {
    -webkit-transform: translate(-300%, -50%);
            transform: translate(-300%, -50%);
    opacity: 0; }
  to {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 1; } }

@keyframes showModal {
  from {
    -webkit-transform: translate(-300%, -50%);
            transform: translate(-300%, -50%);
    opacity: 0; }
  to {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 1; } }

@-webkit-keyframes hideModal {
  from {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 1; }
  to {
    -webkit-transform: translate(-300%, -50%);
            transform: translate(-300%, -50%);
    opacity: 0; } }

@keyframes hideModal {
  from {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 1; }
  to {
    -webkit-transform: translate(-300%, -50%);
            transform: translate(-300%, -50%);
    opacity: 0; } }



