.UserDataItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 55px; }

.UserDataItem_big.UserDataItem_editing {
  -ms-flex-align: start;
      align-items: flex-start; }

.UserDataItem_big .UserDataItem__caption, .UserDataItem_big .UserDataItem__input input {
  font-size: 30px;
  font-family: 'LatoBold', sans-serif; }

.UserDataItem_big .UserDataItem__input input {
  padding: 15px;
  border: 1px solid #cccccc; }

