    -o-transition: all .5s ease;
    transition: all .5s ease; }
  .UserDataItem__btn .UserDataItem__icon_editing {
    fill: #272023; }
  .UserDataItem__btn:hover svg {
    fill: #272023; }
  .UserDataItem__btn:hover .UserDataItem__icon_editing {
    fill: #d40741; }



