.Layout__content {
  margin-top: 60px;
  padding-bottom: 50px; }

.Layout__error {
  text-align: center;
  padding: 50px 10px;
  color: #d40741; }
  @media screen and (max-width: 768px) {
    .Layout__error {
      font-size: 20px; } }

.Layout__btn {
  background: none;
  border: none;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  cursor: pointer;
  display: block;
  margin: 0 auto; }
  .Layout__btn svg {
    width: 50px; }
  .Layout__btn:hover svg {
    fill: #d40741; }



