@font-face {
  font-family: "LatoBold";
  font-style: normal;
  font-weight: normal;
  src: url("../../assets/fonts/LatoBold/LatoBold.eot?#iefix") format("embedded-opentype"), url("../../assets/fonts/LatoBold/LatoBold.woff") format("woff"), url("../../assets/fonts/LatoBold/LatoBold.ttf") format("truetype"); }

@font-face {
  font-family: "BadScriptRegular";
  font-style: normal;
  font-weight: normal;
  src: url("../../assets/fonts/BadScriptRegular/BadScriptRegular.eot?#iefix") format("embedded-opentype"), url("../../assets/fonts/BadScriptRegular/BadScriptRegular.woff") format("woff"), url("../../assets/fonts/BadScriptRegular/BadScriptRegular.ttf") format("truetype"); }

html {
  font-size: 16px; }

body {
  -webkit-font-smoothing: antialiased;
  line-height: 1.42; }

html, body {
  width: 100%;
  height: 100%; }

.wrapper {
  min-height: 100%;
  overflow: hidden;
  position: relative; }

.wrapper:after {
  content: "";
  display: block; }

footer {
  position: relative; }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

img {
  max-width: 100%; }

body {
  font-family: "LatoRegular", sans-serif;
  font-size: 16px;
  color: #272023; }

button:focus {
  outline: none; }



